import { useContext } from 'react';
import { Context as BanksContext } from '../contexts/Banks';
import { Bank, ContractName } from '../tomb-finance';

const useBank = (contractName: ContractName): Bank => {
  const { banks } = useContext(BanksContext);
  if (contractName === "BethEthGenesisPool") {
    return banks.find(bank => bank.contract === "TombGenesisRewardPool" && bank.depositTokenName === "BETH-ETH LP")
  } else if (contractName === "ShareEthGenesisPool") {
    return banks.find(bank => bank.contract === "TombGenesisRewardPool" && bank.depositTokenName === "SHARE-ETH LP")
  } else if (contractName === "EthGenesisPool") {
    return banks.find(bank => bank.contract === "TombGenesisRewardPool" && bank.depositTokenName === "WETH")
  } else if (contractName === "BaldGenesisPool") {
    return banks.find(bank => bank.contract === "TombGenesisRewardPool" && bank.depositTokenName === "BALD")
  } else if (contractName === "BethEthRewardPool") {
    return banks.find(bank => bank.contract === "ShareRewardPool" && bank.depositTokenName === "BETH-ETH LP")
  } else if (contractName === "ShareEthRewardPool") {
    return banks.find(bank => bank.contract === "ShareRewardPool" && bank.depositTokenName === "SHARE-ETH LP")
  } else if (contractName === "EthRewardPool") {
    return banks.find(bank => bank.contract === "ShareRewardPool" && bank.depositTokenName === "WETH")
  }
  return banks.find((bank) => bank.contract === contractName);
};

export default useBank;
